<template>
    <div class="XForgotValidation pa-3 mx-auto">
        <v-card :flat="$parent['scheme'].forgot.flat" :max-width="$parent['scheme'].forgot.steps.validation.width" class="mx-auto">
            <v-card-text class="text-center">
                <v-avatar color="#eee" size="80" class="mt-3">
                    <v-icon size="50">mdi-email-check-outline</v-icon>
                </v-avatar>

                <p class="text-center mb-8 mt-6"> {{ $t($parent['scheme'].forgot.steps.validation.title) }} </p>

                <v-form v-model="$parent['scheme'].forgot.steps.validation.valid">
                    <v-text-field ref="code" v-model="$parent['scheme'].forgot.fields.code.value"
                        :label="$t($parent['scheme'].forgot.fields.code.label)"
                        :disabled="$parent['scheme'].forgot.fields.code.disabled || $parent['scheme'].loading"
                        :placeholder="$t($parent['scheme'].forgot.fields.code.placeholder)"
                        :rules="$parent['scheme'].forgot.fields.code.rules"
                        :dense="$parent['scheme'].forgot.dense"
                        :color="$parent['scheme'].forgot.theme"
                        outlined>
                        <template v-slot:message="{ message }">
                            <span v-html="$t(message)"></span>
                        </template>
                    </v-text-field>

                    <v-btn @click.prevent="$parent.forgotValidation"
                        :loading="$parent['scheme'].loading"
                        :disabled="!$parent['scheme'].forgot.steps.validation.valid || $parent['scheme'].loading"
                        :color="$parent['scheme'].forgot.theme"
                        :dark="$parent['scheme'].forgot.dark" class="mt-2"
                        block depressed> {{ $t('confirm.btn') }} </v-btn>

                    <v-btn @click.prevent="$parent.forgotCancel"
                        :disabled="$parent['scheme'].loading"
                        :color="$parent['scheme'].forgot.theme"
                        text small block
                        class="mt-4"> {{ $t('cancel.btn') }} </v-btn>
                </v-form>
            </v-card-text>
        </v-card>
    </div>
</template>

<script>
export default {
    name: 'XForgotValidation',
    data() {
        return {

        }
    },
    methods: {
        setInputFocus() {
            if (this.$parent['scheme'].forgot.fields.code.visible) {
                setTimeout(() => this.$refs.code.focus());
            }
        },
    },
    mounted() {
        this.setInputFocus();
    },
}
</script>

