<template>
    <div class="XLogin pa-3 fill-width">
        <v-card :flat="$parent['scheme'].login.flat" :max-width="$parent['scheme'].login.width" class="mx-auto">
            <v-card-text class="text-center">
                <slot v-if="!!this.$slots['avatar']" name="avatar"></slot>

                <v-avatar v-else color="tree" size="80" class="mt-3">
                    <v-img src="@/assets/imgs/user.png" max-width="160"></v-img>
                </v-avatar>

                <div v-if="!this.$slots['title']" class="text-center mt-4 mb-8">
                    <div class="text-subtitle-1"> {{ $t($parent['scheme'].login.title) }} </div>
                    <div class="text-subtitle-2 mt-1"> {{ $t($parent['scheme'].login.subTitle) }} </div>
                </div>

                <slot v-else name="title"></slot>

                <v-form @submit.prevent="$parent.login" v-model="$parent['scheme'].login.valid" class="x-login-form">
                    <v-text-field ref="username" v-if="$parent['scheme'].login.fields.username.visible"
                        v-model="$parent['scheme'].login.fields.username.value"
                        :label="$t($parent['scheme'].login.fields.username.label)"
                        :dense="$parent['scheme'].login.dense"
                        :disabled="$parent['scheme'].login.fields.username.disabled || $parent['scheme'].loading"
                        :placeholder="$t($parent['scheme'].login.fields.username.placeholder)"
                        :rules="$parent['scheme'].login.fields.username.rules"
                        :color="$parent['scheme'].login.theme"
                        :loading="$parent['scheme'].login.fields.username.loading"
                        outlined>
                        <template v-slot:message="{ message }">
                            <span v-html="$t(message)"></span>
                        </template>
                    </v-text-field>

                    <v-text-field v-if="$parent['scheme'].login.fields.password.visible"
                        v-model="$parent['scheme'].login.fields.password.value"
                        :label="$t($parent['scheme'].login.fields.password.label)"
                        :placeholder="$parent['scheme'].login.fields.password.placeholder"
                        :type="$parent['scheme'].login.fields.password.showValue ? 'text' : 'password'"
                        :disabled="$parent['scheme'].login.fields.password.disabled || $parent['scheme'].loading"
                        :rules="$parent['scheme'].login.fields.password.rules"
                        :append-icon="$parent['scheme'].login.fields.password.toggleValue ? ($parent['scheme'].login.fields.password.showValue ? 'mdi-eye-off-outline' : 'mdi-eye-outline') : ''"
                        :dense="$parent['scheme'].login.dense"
                        :color="$parent['scheme'].login.theme"
                        :loading="$parent['scheme'].login.fields.password.loading"
                        @click:append="$parent['scheme'].login.fields.password.showValue = !$parent['scheme'].login.fields.password.showValue"
                        outlined>
                        <template v-slot:message="{ message }">
                            <span v-html="$t(message)"></span>
                        </template>
                    </v-text-field>

                    <v-select v-if="$parent['scheme'].login.fields.environment.visible"
                        v-model="$parent['scheme'].login.fields.environment.value"
                        :label="$t($parent['scheme'].login.fields.environment.label)"
                        :items="$parent['scheme'].login.fields.environment.items"
                        :disabled="$parent['scheme'].loading"
                        :dense="$parent['scheme'].login.dense"
                        :color="$parent['scheme'].login.theme"
                        :loading="$parent['scheme'].login.fields.environment.loading"
                        return-object
                        outlined></v-select>

                    <v-btn v-if="$parent['scheme'].login.forgotBtn" @click.prevent="$parent.forgotStart" :disabled="$parent['scheme'].loading" text small :color="$parent['scheme'].login.theme"> {{ $t('login.forgot.btn') }} </v-btn>

                    <v-btn type="submit" :disabled="!$parent['scheme'].login.valid || $parent['scheme'].loading" :loading="$parent['scheme'].loading" :color="$parent['scheme'].login.theme" block depressed class="mt-4"> {{ $t('login.enter.btn') }} </v-btn>
                </v-form>
            </v-card-text>
        </v-card>
    </div>
</template>

<script>
export default {
    name: 'XLogin',
    data() {
        return {

        }
    },
    methods: {
        setInputFocus() {
            if (this.$parent['scheme'].login.fields.username.visible) {
                setTimeout(() => this.$refs.username.focus());
            }
        },
    },
    mounted() {
        this.$parent.logout();
        this.setInputFocus();
    },
}
</script>

