<template>
    <div :class="['x-authenticator full-page', $vuetify.theme.isDark ? 'theme--dark' : 'theme--light']">
        <v-row no-gutters class="fill-height">
            <v-col lg="8" v-if="scheme.splash.visible" :class="['x-left-panel d-none d-lg-flex align-center justify-center', scheme.splash.theme ? scheme.splash.theme : '']">
                <x-splash-screen>
                    <slot name="splash"></slot>
                </x-splash-screen>
            </v-col>

            <v-col :lg="scheme.splash.visible ? 4 : 12" :class="['x-right-panel d-flex flex-column align-center justify-center', scheme.login.bgTheme ? scheme.login.bgTheme : '']">
                <div class="mt-2">
                    <span :class="['text-caption', {'white--text': scheme.login.dark || $vuetify.theme.isDark }]"> {{ $t('version') }}: {{ scheme.info.version }} </span>
                </div>

                <v-slide-y-reverse-transition hide-on-leave>
                    <x-login v-if="scheme.step === 0" class="fill-width">
                        <template slot="title">
                            <slot name="title"></slot>
                        </template>
                    </x-login>
                </v-slide-y-reverse-transition>

                <v-slide-y-reverse-transition hide-on-leave>
                    <x-forgot-sendMail v-if="scheme.step === 1" class="fill-width"></x-forgot-sendMail>
                </v-slide-y-reverse-transition>

                <v-slide-y-reverse-transition hide-on-leave>
                    <x-forgot-validation v-if="scheme.step === 2" class="fill-width"></x-forgot-validation>
                </v-slide-y-reverse-transition>

                <v-slide-y-reverse-transition hide-on-leave>
                    <x-forgot-reset-pwd v-if="scheme.step === 3" class="fill-width"></x-forgot-reset-pwd>
                </v-slide-y-reverse-transition>

                <v-slide-y-reverse-transition hide-on-leave>
                    <x-forgot-resume v-if="scheme.step === 4" class="fill-width"></x-forgot-resume>
                </v-slide-y-reverse-transition>

                <x-translator v-if="scheme.translate.visible" class="py-2"></x-translator>

            </v-col>
        </v-row>
    </div>
</template>

<script>
import XSplashScreen from './SplashScreen.vue';
import XLogin from './Login.vue';
import XForgotSendMail from './ForgotSendMail.vue';
import XForgotValidation from './ForgotValidation.vue';
import XForgotResetPwd from './ForgotResetPwd.vue';
import XForgotResume from './ForgotResume.vue';

import { recursiveMerge } from '../../commons';
import schemeModel from './scheme.model';

export default {
    name: 'Authenticator',
    components: {
        XSplashScreen,
        XLogin,
        XForgotSendMail,
        XForgotValidation,
        XForgotResetPwd,
        XForgotResume,
    },
    props: {
        value: {
            type: Object
        },
    },
    data() {
        return {
            scheme: schemeModel,
        }
    },
    computed: {
        credentialEvent() {
            return {
                currentStep: this.scheme.step,
                loading: this.loading,
                fields: {
                    username: this.scheme.login.fields.username.value,
                    password: this.scheme.login.fields.password.value,
                    environment: this.scheme.login.fields.environment.value,
                }
            }
        },
        forgotEvent() {
            return {
                currentStep: this.scheme.step,
                nextStep: this.scheme.step + 1,
                setStep: this.setStep,
                next: this.nextStep,
                loading: this.loading,
                fields: {
                    email: this.scheme.forgot.fields.email.value,
                    code: this.scheme.forgot.fields.code.value,
                    newPwd: this.scheme.forgot.fields.newPwd.value,
                    confirmPwd: this.scheme.forgot.fields.confirmPwd.value
                }
            }
        },
        logoutEvent() {
            return {
                currentStep: this.scheme.step,
                nextStep: this.scheme.step,
                setStep: this.setStep,
                loading: this.loading,
            }
        },
    },
    methods: {
        schemeMerge() {
            if (this.value) {
                this.scheme = recursiveMerge(this.scheme, this.value);
            }
            this.updateModel();
        },

        updateModel() {
            this.$emit('input', this.scheme);
        },

        setStep(step) {
            this.scheme.step = step;
            this.updateModel();
        },

        nextStep() {
            this.setStep(this.scheme.step + 1);
        },

        loading(action) {
            if (action == null) {
                return this.scheme.loading = true;
            }
            this.scheme.loading = action;
        },

        login() {
            if (this.scheme.login.onEnter) {
                return this.scheme.login.onEnter(this.credentialEvent);
            }
            this.$emit('login', this.credentialEvent);
        },

        logout() {
            if (this.scheme.login.onLogout) {
                return this.scheme.login.onLogout(this.logoutEvent);
            }
            this.$emit('logout', this.logoutEvent);
        },

        forgotStart() {
            this.setStep(1);
        },

        forgotSendMail() {
            if (this.scheme.forgot.onSendMail) {
                return this.scheme.forgot.onSendMail(this.forgotEvent);
            }
            this.$emit('forgot', this.forgotEvent);
        },

        forgotValidation() {
            if (this.scheme.forgot.onValidation) {
                return this.scheme.forgot.onValidation(this.forgotEvent);
            }
            this.$emit('forgot', this.forgotEvent);
        },

        forgotResetPwd() {
            if (this.scheme.forgot.onResetPwd) {
                return this.scheme.forgot.onResetPwd(this.forgotEvent);
            }
            this.$emit('forgot', this.forgotEvent);
        },

        forgotComplete() {
            if (this.scheme.forgot.onCompleted) {
                return this.scheme.forgot.onCompleted(this.forgotEvent);
            }
            this.$emit('forgot', this.forgotEvent);
        },

        forgotCancel() {
            // Limpar dados
            this.setStep(0);
        },
    },
    created() {
        this.schemeMerge();
    },
    // watch: {
    //     'value': {
    //         deep: true,
    //         handler(newVal) {
    //             this.schemeMerge();
    //         },
    //     }
    // },
}
</script>

<style scoped>
.x-authenticator.theme--light .x-left-panel{
    border-right: 1px solid rgba(0, 0, 0, 0.12) !important;
}
.x-authenticator.theme--dark .x-left-panel{
    border-right: 1px solid rgba(255, 255, 255, 0.12) !important;
}
</style>
