<template>
    <div class="XForgotResetPwd pa-3 mx-auto">
        <v-card :flat="$parent['scheme'].forgot.flat" :max-width="$parent['scheme'].forgot.steps.resetPwd.width" class="mx-auto">
            <v-card-text class="text-center">
                <v-avatar color="#eee" size="80" class="mb-4 mt-3">
                    <v-img src="@/assets/imgs/user.png" max-width="160"></v-img>
                </v-avatar>

                <p class="text-h5 font-weight-light text-center mb-2"> {{ $parent['scheme'].forgot.user.firstName }} </p>

                <p class="text-center mb-6"> {{ $t($parent['scheme'].forgot.steps.resetPwd.title) }} </p>

                <v-form v-model="$parent['scheme'].forgot.steps.resetPwd.valid">
                    <v-text-field ref="newPwd" v-model="$parent['scheme'].forgot.fields.newPwd.value"
                        :type="$parent['scheme'].forgot.fields.newPwd.showValue ? 'text' : 'password'"
                        :label="$t($parent['scheme'].forgot.fields.newPwd.label)"
                        :disabled="$parent['scheme'].forgot.fields.newPwd.disabled || $parent['scheme'].loading"
                        :placeholder="$t($parent['scheme'].forgot.fields.newPwd.placeholder)"
                        :rules="$parent['scheme'].forgot.fields.newPwd.rules"
                        :append-icon="$parent['scheme'].forgot.fields.newPwd.toggleValue ? ($parent['scheme'].forgot.fields.newPwd.showValue ? 'mdi-eye-off-outline' : 'mdi-eye-outline') : ''"
                        @click:append="$parent['scheme'].forgot.fields.newPwd.showValue = !$parent['scheme'].forgot.fields.newPwd.showValue"
                        :dense="$parent['scheme'].forgot.dense"
                        :color="$parent['scheme'].forgot.theme"
                        outlined
                        class="mb-3">
                        <template v-slot:message="{ message }">
                            <span v-html="$t(message)"></span>
                        </template>
                    </v-text-field>

                    <v-text-field v-model="$parent['scheme'].forgot.fields.confirmPwd.value"
                        :type="$parent['scheme'].forgot.fields.confirmPwd.showValue ? 'text' : 'password'"
                        :label="$t($parent['scheme'].forgot.fields.confirmPwd.label)"
                        :disabled="$parent['scheme'].forgot.fields.confirmPwd.disabled || $parent['scheme'].loading"
                        :placeholder="$t($parent['scheme'].forgot.fields.confirmPwd.placeholder)"
                        :rules="$parent['scheme'].forgot.fields.confirmPwd.rules"
                        :append-icon="$parent['scheme'].forgot.fields.confirmPwd.toggleValue ? ($parent['scheme'].forgot.fields.confirmPwd.showValue ? 'mdi-eye-off-outline' : 'mdi-eye-outline') : ''"
                        @click:append="$parent['scheme'].forgot.fields.confirmPwd.showValue = !$parent['scheme'].forgot.fields.confirmPwd.showValue"
                        :dense="$parent['scheme'].forgot.dense"
                        :color="$parent['scheme'].forgot.theme"
                        outlined>
                        <template v-slot:message="{ message }">
                            <span v-html="$t(message)"></span>
                        </template>
                    </v-text-field>

                    <v-btn @click="$parent.forgotResetPwd"
                        :loading="$parent['scheme'].loading"
                        :disabled="!$parent['scheme'].forgot.steps.resetPwd.valid || $parent['scheme'].loading"
                        :color="$parent['scheme'].forgot.theme"
                        :dark="$parent['scheme'].forgot.dark"
                        block
                        depressed
                        class="mt-2"> {{ $t('confirm.btn') }} </v-btn>

                    <v-btn @click.prevent="$parent.forgotCancel"
                        :disabled="$parent['scheme'].loading"
                        :color="$parent['scheme'].forgot.theme"
                        text small block
                        class="mt-4"> {{ $t('cancel.btn') }} </v-btn>
                </v-form>
            </v-card-text>
        </v-card>
    </div>
</template>

<script>
export default {
    name: 'XForgotResetPwd',
    data() {
        return {

        }
    },
    methods: {
        setInputFocus() {
            if (this.$parent['scheme'].forgot.fields.newPwd.visible) {
                setTimeout(() => this.$refs.newPwd.focus());
            }
        },
    },
    mounted() {
        this.setInputFocus();
    },
}
</script>

