<template>
    <div class="XForgotSendMail pa-3 fill-width">
        <v-card :flat="$parent['scheme'].forgot.flat" :max-width="$parent['scheme'].forgot.steps.sendMail.width" class="mx-auto">
            <v-card-text class="text-center">
                <v-avatar color="#eee" size="80" class="mb-3 mt-3">
                    <v-icon size="50">mdi-email-arrow-right</v-icon>
                </v-avatar>

                <p class="mb-6 text-center"> {{ $t($parent['scheme'].forgot.steps.sendMail.title) }} </p>

                <v-form v-model="$parent['scheme'].forgot.valid">
                    <v-text-field ref="email" v-if="$parent['scheme'].forgot.fields.email.visible"
                        v-model="$parent['scheme'].forgot.fields.email.value"
                        :label="$t($parent['scheme'].forgot.fields.email.label)"
                        :disabled="$parent['scheme'].forgot.fields.email.disabled || $parent['scheme'].loading"
                        :placeholder="$t($parent['scheme'].forgot.fields.email.placeholder)"
                        :rules="$parent['scheme'].forgot.fields.email.rules"
                        :dense="$parent['scheme'].forgot.dense"
                        :color="$parent['scheme'].forgot.theme"
                        outlined>
                        <template v-slot:message="{ message }">
                            <span v-html="$t(message)"></span>
                        </template>
                    </v-text-field>

                    <v-btn @click.prevent="$parent.forgotSendMail" :loading="$parent['scheme'].loading" :disabled="!$parent['scheme'].forgot.steps.sendMail.valid || $parent['scheme'].loading" block depressed :color="$parent['scheme'].forgot.theme" :dark="$parent['scheme'].forgot.dark" class="mt-2"> {{ $t('confirm.btn') }} </v-btn>

                    <v-btn @click.prevent="$parent.forgotCancel" :disabled="$parent['scheme'].loading" text small block :color="$parent['scheme'].forgot.theme" class="mt-4"> {{ $t('cancel.btn') }} </v-btn>
                </v-form>
            </v-card-text>
        </v-card>
    </div>
</template>

<script>
export default {
    name: 'XForgotSendMail',
    data() {
        return {

        }
    },
    methods: {
        setInputFocus() {
            if (this.$parent['scheme'].forgot.fields.email.visible) {
                setTimeout(() => this.$refs.email.focus());
            }
        },
    },
    mounted() {
        this.setInputFocus();
    },
}
</script>

